import React from 'react';

import classes from './Portfolio.css';
import Shiperd from '../../assets/images/shiperd.png';
import Sopotra from '../../assets/images/sopotra.png';
import Waveum from '../../assets/images/waveum.png';

const portfolio = (props) => (
  <div className={classes.Projects}>
    <article className={classes.Item} style={{backgroundImage: `url(${Shiperd})`}}>
      <div className={classes.ItemInfoContainer}>
        <h1 className={classes.ItemTitle}>Shiperd</h1>
        <span className={classes.ItemDescription}>
          <p><strong>Technology</strong>: Scala, AWS, AngularJS</p>
          <p><strong>Goal</strong>: Provide online shipping management software that automates the daily workflow of importers and exporters.</p>
          <p><a target="_blank" rel="noopener noreferrer"
                href="http://www.shiperd.com/"><strong>Check it out</strong></a></p>
        </span>
      </div>
    </article>
    <article className={classes.Item} style={{backgroundImage: `url(${Sopotra})`}}>
      <div className={classes.ItemInfoContainer}>
        <h1 className={classes.ItemTitle}>Sopotra</h1>
        <span className={classes.ItemDescription}>
          <p><strong>Technology</strong>: Java, Spring, AngularJS</p>
          <p><strong>Goal</strong>: Provide affordable Ways to Ship, Travel, and Make Money.</p>
          <p><a target="_blank" rel="noopener noreferrer"
                href="http://sopotra.com/"><strong>Check it out</strong></a></p>
        </span>
      </div>
    </article>
    <article className={classes.Item} style={{backgroundImage: `url(${Waveum})`}}>
      <div className={classes.ItemInfoContainer}>
        <h1 className={classes.ItemTitle}>Waveum</h1>
        <span className={classes.ItemDescription}>
          <p><strong>Technology</strong>: Java, Spring Boot, Hibernate, AngularJS</p>
          <p><strong>Goal</strong>: Provide secure sharing network.</p>
          <p><a target="_blank" rel="noopener noreferrer"
                href="https://www.waveum.com/"><strong>Check it out</strong></a></p>
        </span>
      </div>
    </article>
  </div>
);

export default portfolio;
