import React from 'react';
import classes from './Social.css';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub, faStackOverflow, faLinkedin, faTwitter } from '@fortawesome/free-brands-svg-icons';

const social = (props) => (
  <div className={classes.Social}>
    <a target="_blank" rel="noopener noreferrer" href="https://github.com/niteshvirani">
      <FontAwesomeIcon icon={faGithub}/>
    </a>
    <a target="_blank" rel="noopener noreferrer" href="https://stackoverflow.com/users/1371499/nitesh-virani">
      <FontAwesomeIcon icon={faStackOverflow}/>
    </a>
    <a target="_blank" rel="noopener noreferrer" href="https://www.linkedin.com/in/niteshvirani/">
      <FontAwesomeIcon icon={faLinkedin}/>
    </a>
    <a target="_blank" rel="noopener noreferrer" href="https://twitter.com/niteshvirani">
      <FontAwesomeIcon icon={faTwitter}/>
    </a>
  </div>
);

export default social;
