import React from 'react';

import classes from './About.css';

const about = (props) => {
  return (
    <div className={classes.About}>
      <div className={classes.AboutContent}>
        <p>
          I like Java and JavaScript. I write <span className={classes.Highlight}>Java[Script]</span> everyday.
        </p>
        <p>
          I would like to stay close to the latest web technologies. I am obsessed with making the things perfect.
        </p>
        <p>
          I built this site with <span className="highlight">React</span>.
        </p>
        <p>
          Java, Javascript, Spring, React, Redux, Angular, AngularJS, Amazon AWS, Docker are my main skills.
        </p>
      </div>
    </div>
  );
};

export default about;
